import {SET_X_DETAILS, SET_GRAPH_DETAILS} from './actionTypes'
import {SOC} from '../reducers/graphTypes'

export const reducer = (state= {
    vehicleId:[],
    date:['01/01/YYYY','28/12/YYYY'],
    time:['00:00:00','23:59:59'],
    graph_type:SOC,
    }, action) => {
    switch(action.type){
        case SET_X_DETAILS:
            return {
                ...state,
                vehicleId:action.payload.vehicleId,
                date:action.payload.date,
                time:action.payload.time
            }
        case SET_GRAPH_DETAILS:
            return{
                ...state,
                graph_type:action.payload.graph,
            }
        default:
            return state
    }
}